import { NcPopover, NcTextField, NcAvatar, NcEmptyContent, NcButton } from '@nextcloud/vue';
import AlertCircleOutline from 'vue-material-design-icons/AlertCircleOutline.vue';
import Magnify from 'vue-material-design-icons/Magnify.vue';
export default {
  name: 'SearchableList',
  components: {
    NcPopover,
    NcTextField,
    Magnify,
    AlertCircleOutline,
    NcAvatar,
    NcEmptyContent,
    NcButton
  },
  props: {
    labelText: {
      type: String,
      default: 'this is a label'
    },
    searchList: {
      type: Array,
      required: true
    },
    emptyContentText: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      opened: false,
      error: false,
      searchTerm: ''
    };
  },
  computed: {
    filteredList() {
      return this.searchList.filter(element => {
        if (!this.searchTerm.toLowerCase().length) {
          return true;
        }
        return ['displayName'].some(prop => element[prop].toLowerCase().includes(this.searchTerm.toLowerCase()));
      });
    }
  },
  methods: {
    clearSearch() {
      this.searchTerm = '';
    },
    itemSelected(element) {
      this.$emit('item-selected', element);
      this.clearSearch();
      this.opened = false;
    },
    searchTermChanged(term) {
      this.$emit('search-term-change', term);
    }
  }
};