import NcListItem from '@nextcloud/vue/dist/Components/NcListItem.js';
export default {
  name: 'SearchResult',
  components: {
    NcListItem
  },
  props: {
    thumbnailUrl: {
      type: String,
      default: null
    },
    title: {
      type: String,
      required: true
    },
    subline: {
      type: String,
      default: null
    },
    resourceUrl: {
      type: String,
      default: null
    },
    icon: {
      type: String,
      default: ''
    },
    rounded: {
      type: Boolean,
      default: false
    },
    query: {
      type: String,
      default: ''
    },
    /**
     * Only used for the first result as a visual feedback
     * so we can keep the search input focused but pressing
     * enter still opens the first result
     */
    focused: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      thumbnailHasError: false
    };
  },
  watch: {
    thumbnailUrl() {
      this.thumbnailHasError = false;
    }
  },
  methods: {
    isValidIconOrPreviewUrl(url) {
      return /^https?:\/\//.test(url) || url.startsWith('/');
    },
    thumbnailErrorHandler() {
      this.thumbnailHasError = true;
    }
  }
};