import { defineStore } from 'pinia';
export const useSearchStore = defineStore({
  id: 'search',
  state: () => ({
    externalFilters: []
  }),
  actions: {
    registerExternalFilter(_ref) {
      let {
        id,
        appId,
        label,
        callback,
        icon
      } = _ref;
      this.externalFilters.push({
        id,
        appId,
        name: label,
        callback,
        icon,
        isPluginFilter: true
      });
    }
  }
});